            </view>
          </view>
          <view class="info-item">
            <text class="info-label">ÊÄªÈáëÈ¢ùÔºö</text>
            <text class="info-value amount">¬•{{ orderDetail.totalCost || orderDetail.total_fee || 0 }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">ÂàõÂª∫Êó∂Èó¥Ôºö</text>
            <text class="info-value">{{ formatDate(orderDetail.createTime || orderDetail.create_time) }}</text>
          </view>
          <view class="info-item">
            <text class="info-label">Êä•Âêç‰∫∫Êï∞Ôºö</text>
            <text class="info-value">{{ getApplicantCount(orderDetail) }}‰∫∫</text>
          </view>
        </view>
      </view>

      <!-- Êä•Âêç‰∫∫‰ø°ÊÅØ -->
      <view v-if="orderDetail.applicants && orderDetail.applicants.length > 0" class="info-card">
        <view class="card-header">
          <text class="card-title">üë• Êä•Âêç‰∫∫‰ø°ÊÅØ</text>
        </view>
        <view class="applicants-list">
          <view 
            v-for="(applicant, index) in orderDetail.applicants" 
            :key="index"
            class="applicant-item"
          >
            <view class="applicant-header">
              <text class="applicant-title">Êä•Âêç‰∫∫ {{ index + 1 }}</text>
            </view>
            <view class="applicant-info">
              <view class="info-row">
                <text class="info-label">ÂßìÂêçÔºö</text>
                <text class="info-value">{{ applicant.name || 'Êú™Â°´ÂÜô' }}</text>
              </view>
              <view class="info-row">
                <text class="info-label">ÁîµËØùÔºö</text>
                <text class="info-value">{{ applicant.phone || 'Êú™Â°´ÂÜô' }}</text>
              </view>
              <view class="info-row" v-if="applicant.idCard">
                <text class="info-label">Ë∫´‰ªΩËØÅÔºö</text>
                <text class="info-value">{{ applicant.idCard }}</text>
              </view>
            </view>
          </view>
        </view>
      </view>

      <!-- Êìç‰ΩúËÆ∞ÂΩï -->
      <view class="info-card">
        <view class="card-header">
          <text class="card-title">üìù Êìç‰ΩúËÆ∞ÂΩï</text>
        </view>
        <view class="operations-list">
          <view v-if="orderDetail.operations && orderDetail.operations.length > 0">
            <view 
              v-for="(operation, index) in orderDetail.operations" 
              :key="index"
              class="operation-item"
            >
              <view class="operation-header">
                <text class="operation-type">{{ operation.type }}</text>
                <text class="operation-time">{{ formatDate(operation.time) }}</text>
              </view>
              <view class="operation-content">
                <text class="operation-operator">Êìç‰Ωú‰∫∫Ôºö{{ operation.operator }}</text>
                <text v-if="operation.remark" class="operation-remark">Â§áÊ≥®Ôºö{{ operation.remark }}</text>
              </view>
            </view>
          </view>
          <view v-else class="empty-operations">
            <text class="empty-text">ÊöÇÊó†Êìç‰ΩúËÆ∞ÂΩï</text>
          </view>
        </view>
      </view>

      <!-- Â§áÊ≥®ÁÆ°ÁêÜ -->
      <view class="info-card">
        <view class="card-header">
          <text class="card-title">üí¨ Â§áÊ≥®ÁÆ°ÁêÜ</text>
        </view>
        <view class="remark-section">
          <view class="remark-input-area">
            <textarea 
              v-model="newRemark"
              placeholder="ËØ∑ËæìÂÖ•Â§áÊ≥®ÂÜÖÂÆπ..."
              class="remark-input"
              maxlength="500"
            />
            <view class="remark-counter">
              <text class="counter-text">{{ newRemark.length }}/500</text>
            </view>
          </view>
          <button class="add-remark-btn" @click="addRemark" :disabled="!newRemark.trim()">
            <text class="btn-icon">‚ûï</text>
            <text>Ê∑ªÂä†Â§áÊ≥®</text>
          </button>
        </view>
      </view>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <view class="action-buttons">
        <button 
          v-if="orderDetail.status === 'ÂæÖÁ°ÆËÆ§'" 
          class="action-btn confirm-btn" 
          @click="confirmOrder"
        >
          <text class="btn-icon">‚úÖ</text>
          <text>Á°ÆËÆ§Ê≠§ÂêàÂùõÊ≥ï‰ºö</text>
        </button>
        <button 
          v-if="orderDetail.status === 'ÂæÖÁ°ÆËÆ§'" 
          class="action-btn process-btn" 
          @click="processOrder"
        >
          <text class="btn-icon">‚ö°</text>
          <text>ÂºÄÂßãÂ§ÑÁêÜ</text>
        </button>
        <button 
          v-if="orderDetail.status === 'Â§ÑÁêÜ‰∏≠'" 
          class="action-btn complete-btn" 
          @click="completeOrder"
        >
          <text class="btn-icon">üéâ</text>
          <text>ÂÆåÊàêËÆ¢Âçï</text>
        </button>
