    </view>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <view v-else class="error-state">
      <view class="error-content">
        <text class="error-icon">‚ùå</text>
        <text class="error-title">ËÆ¢Âçï‰∏çÂ≠òÂú®</text>
        <text class="error-desc">Êó†Ê≥ïÊâæÂà∞ÊåáÂÆöÁöÑËÆ¢Âçï‰ø°ÊÅØ</text>
        <button class="retry-btn" @click="loadOrderDetail">
          <text>ÈáçÊñ∞Âä†ËΩΩ</text>
        </button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      orderId: '',
      orderDetail: null,
      loading: true,
      newRemark: ''
    }
  },

  onLoad(options) {
    console.log('üìã ËÆ¢ÂçïËØ¶ÊÉÖÈ°µÈù¢Âä†ËΩΩÔºåÂèÇÊï∞:', options)
    console.log('üìã È°µÈù¢Ë∑ØÂæÑ:', getCurrentPages()[getCurrentPages().length - 1].route)
    
    if (options.id) {
      this.orderId = options.id
      console.log('üìã Êé•Êî∂Âà∞ÁöÑËÆ¢ÂçïID:', this.orderId)
      this.loadOrderDetail()
    } else {
      console.error('‚ùå ËÆ¢ÂçïIDÂèÇÊï∞‰∏çÂ≠òÂú®')
      uni.showToast({
        title: 'ËÆ¢ÂçïID‰∏çÂ≠òÂú®',
        icon: 'none'
      })
      setTimeout(() => {
        this.goBack()
      }, 1500)
    }
  },

  methods: {
    async loadOrderDetail() {
      try {
        this.loading = true
        console.log('Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÔºåID:', this.orderId)
        
        const jointManagement = uniCloud.importObject('joint-management')
        const result = await jointManagement.getOrderDetail({ orderId: this.orderId })
        
        if (result.success && result.data) {
          this.orderDetail = result.data
          console.log('ËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÊàêÂäü:', this.orderDetail)
        } else {
          this.orderDetail = null
          console.error('ËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÂ§±Ë¥•:', result.message)
          uni.showToast({
            title: result.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error)
        this.orderDetail = null
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },

    getStatusClass(status) {
      const statusMap = {
        'ÂæÖÊîØ‰ªò': 'status-pending',
        'ÂæÖÁ°ÆËÆ§': 'status-waiting',
        'Â§ÑÁêÜ‰∏≠': 'status-processing',
        'Â∑≤ÂÆåÊàê': 'status-completed',
        'Â∑≤ÂèñÊ∂à': 'status-cancelled'
      }
      return statusMap[status] || 'status-default'
    },

    getApplicantCount(order) {
      if (order.applicant_count !== undefined) {
        return order.applicant_count
      }
      if (order.applicants && Array.isArray(order.applicants)) {
        return order.applicants.length
      }
      return 0
    },

    formatDate(date) {
      if (!date) return 'Êú™Áü•Êó∂Èó¥'
      
      const d = new Date(date)
      return d.toLocaleString('zh-CN')
    },

    async confirmOrder() {
      try {
        uni.showModal({
          title: 'Á°ÆËÆ§Êìç‰Ωú',
          content: 'Á°ÆÂÆöË¶ÅÁ°ÆËÆ§Ê≠§ÂêàÂùõÊ≥ï‰ºöÂêóÔºüÊ≠§Êìç‰ΩúÂ∞ÜËÆ∞ÂΩïÊìç‰Ωú‰∫∫ÂíåÊìç‰ΩúÊó∂Èó¥„ÄÇ',
          confirmText: 'Á°ÆËÆ§',
          confirmColor: '#007aff',
          success: async (res) => {
            if (res.confirm) {
              await this.performOrderAction('confirm', 'Á°ÆËÆ§Ê≠§ÂêàÂùõÊ≥ï‰ºö')
            }
          }
        })
      } catch (error) {
        console.error('Á°ÆËÆ§ËÆ¢ÂçïÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },

    async processOrder() {
      try {
        uni.showModal({
          title: 'ÂºÄÂßãÂ§ÑÁêÜ',
          content: 'Á°ÆÂÆöË¶ÅÂºÄÂßãÂ§ÑÁêÜÊ≠§ËÆ¢ÂçïÂêóÔºü',
          confirmText: 'ÂºÄÂßãÂ§ÑÁêÜ',
          confirmColor: '#007aff',
          success: async (res) => {
            if (res.confirm) {
              await this.performOrderAction('process', 'ÂºÄÂßãÂ§ÑÁêÜËÆ¢Âçï')
            }
          }
        })
      } catch (error) {
        console.error('ÂºÄÂßãÂ§ÑÁêÜËÆ¢ÂçïÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },

    async completeOrder() {
      try {
        uni.showModal({
          title: 'ÂÆåÊàêËÆ¢Âçï',
          content: 'Á°ÆÂÆöË¶ÅÂÆåÊàêÊ≠§ËÆ¢ÂçïÂêóÔºü',
          confirmText: 'ÂÆåÊàê',
          confirmColor: '#007aff',
          success: async (res) => {
            if (res.confirm) {
              await this.performOrderAction('complete', 'ÂÆåÊàêËÆ¢Âçï')
            }
          }
        })
      } catch (error) {
        console.error('ÂÆåÊàêËÆ¢ÂçïÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },

    async performOrderAction(action, actionName) {
      try {
        uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' })
        
        const jointManagement = uniCloud.importObject('joint-management')
        const result = await jointManagement.performOrderAction({
          orderId: this.orderId,
          action: action,
          actionName: actionName,
          operator: 'ÁÆ°ÁêÜÂëò', // ËøôÈáåÂèØ‰ª•‰ªéÁî®Êà∑‰ø°ÊÅØËé∑Âèñ
          remark: this.newRemark.trim() || undefined
        })
        
        if (result.success) {
          uni.hideLoading()
          uni.showToast({
            title: 'Êìç‰ΩúÊàêÂäü',
            icon: 'success'
          })
          
          // Ê∏ÖÁ©∫Â§áÊ≥®ËæìÂÖ•
          this.newRemark = ''
          
          // ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
          await this.loadOrderDetail()
        } else {
          uni.hideLoading()
          uni.showToast({
            title: result.message || 'Êìç‰ΩúÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        console.error('ÊâßË°åËÆ¢ÂçïÊìç‰ΩúÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },

    async addRemark() {
      console.log('üí¨ ÂºÄÂßãÊ∑ªÂä†Â§áÊ≥®...')
      console.log('üí¨ Â§áÊ≥®ÂÜÖÂÆπ:', this.newRemark)
      console.log('üí¨ ËÆ¢ÂçïID:', this.orderId)
      
      if (!this.newRemark.trim()) {
        console.log('‚ùå Â§áÊ≥®ÂÜÖÂÆπ‰∏∫Á©∫')
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Â§áÊ≥®ÂÜÖÂÆπ',
          icon: 'none'
        })
        return
      }

      try {
        uni.showLoading({ title: 'Ê∑ªÂä†Â§áÊ≥®‰∏≠...' })
        
        const jointManagement = uniCloud.importObject('joint-management')
        console.log('üí¨ ÂáÜÂ§áË∞ÉÁî® addOrderRemark...')
        
        const params = {
          orderId: this.orderId,
          remark: this.newRemark.trim(),
          operator: 'ÁÆ°ÁêÜÂëò'
        }
        console.log('üí¨ Ë∞ÉÁî®ÂèÇÊï∞:', params)
        
        const result = await jointManagement.addOrderRemark(params)
        console.log('üí¨ Â§áÊ≥®Ê∑ªÂä†ÁªìÊûú:', result)
        
        if (result.success) {
          uni.hideLoading()
          console.log('‚úÖ Â§áÊ≥®Ê∑ªÂä†ÊàêÂäü')
          uni.showToast({
            title: 'Â§áÊ≥®Ê∑ªÂä†ÊàêÂäü',
            icon: 'success'
          })
          
          // Ê∏ÖÁ©∫Â§áÊ≥®ËæìÂÖ•
          this.newRemark = ''
          
          // ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
          console.log('üîÑ ÈáçÊñ∞Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ...')
          await this.loadOrderDetail()
        } else {
          uni.hideLoading()
          console.error('‚ùå Â§áÊ≥®Ê∑ªÂä†Â§±Ë¥•:', result.message)
          uni.showToast({
            title: result.message || 'Ê∑ªÂä†Â§áÊ≥®Â§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        console.error('‚ùå Ê∑ªÂä†Â§áÊ≥®ÂºÇÂ∏∏:', error)
        console.error('‚ùå ÈîôËØØÁ±ªÂûã:', error.constructor.name)
        console.error('‚ùå ÈîôËØØ‰ø°ÊÅØ:', error.message)
        uni.showToast({
          title: 'Ê∑ªÂä†Â§áÊ≥®Â§±Ë¥•',
          icon: 'none'
        })
      }
    },

    editOrder() {
      uni.navigateTo({
        url: `/pages/admin/joint/editOrder?id=${this.orderId}`
      })
    },

    goBack() {
      uni.navigateBack()
    }
  }
}
</script>

